.payment-required-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #fff;
  text-align: center;
  padding: 20px;
}

.payment-title {
  font-size: 24px;
  color: #cc0000;
  margin-bottom: 16px;
}

.payment-message {
  color: #444;
  margin-bottom: 24px;
  max-width: 400px;
}

.payment-button {
  background-color: #cc0000;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.payment-button:hover {
  background-color: #a30000;
}
